import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import '../../assets/styles/animations.scss'
import './WonderCard.scss'


class WonderCard extends Component {
    constructor(props) {
        super(props);
        this.state = { elements: this.props.elements }
    }

    componentDidMount() {
    }

    componentWillUnmount() {
    }

    render() {
        return (
            <div className="Wonder-card">
                <div className="tile">
                    <h2 className="header">We build Robust Enterprice Digital Products.</h2>
                    <p className="subtitle"></p>
                </div>
                <div className="content"></div>
            </div>
        )
    }
}

  
export default WonderCard;